.main{
    position:absolute;
    top:10vh;
    left:2.5vh;
    height:-webkit-fit-content !important;
    height:-moz-fit-content !important;
    height:fit-content !important;
}

.welcome-message{
    font-size: 100px;
    position:absolute;
    color:white;
    width:320px;
    top:120px;
    left:650px;
    font-weight:700
}

 .welcome-message p {
    background:black;
  border-right: .15em solid orange;
  font-family: "Courier";
  font-size: 24px;
  white-space: nowrap;
  overflow: hidden;
}


@media only screen and (max-width: 400px)  {
    .welcome-message{
        width: 188px;
        left: 130px;
        font-size: 19px;
        -webkit-transform:rotate(30deg);
                transform:rotate(30deg);
        position:absolute;
        color:grey;
        top:420px;
    }
    
     .welcome-message p {
      border-right: .15em solid orange;
      font-family: "Courier";
      font-size: 14px;
      white-space: nowrap;
      overflow: hidden;
      background:none;
    }

}




.welcome-message p:nth-child(1) {
  width: 13.3em;
  -webkit-animation: type 4s steps(20, end);
  animation: type 4s steps(20, end);
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
}

.welcome-message p:nth-child(2) {
  width: 11.5em;
  opacity: 0;
  -webkit-animation: type2 5s steps(20, end);
  animation: type2 5s steps(20, end);
  -webkit-animation-delay: 5s;
  animation-delay: 5s;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
}

.welcome-message p:nth-child(3) {
  width: 9.9em;
  opacity: 0;
  -webkit-animation: type2 5s steps(20, end);
  animation: type2 5s steps(20, end);
  -webkit-animation-delay: 10s;
  animation-delay: 10s;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
}
.welcome-message p:nth-child(4) {
    width: 11.3em;
    opacity: 0;
    -webkit-animation: type3 5s steps(20, end), blink .5s step-end infinite alternate;
    animation: type3 5s steps(20, end), blink .5s step-end infinite alternate;
    -webkit-animation-delay: 15s;
    animation-delay: 15s;
    -webkit-animation-fill-mode: forwards;
    animation-fill-mode: forwards;
  }

@keyframes type {
  0% {
    width: 0;
  }
  99.9% {
    border-right: .15em solid orange;
  }
  100% {
    border: none;
  }
}

@-webkit-keyframes type {
  0% {
    width: 0;
  }
  99.9% {
    border-right: .15em solid orange;
  }
  100% {
    border: none;
  }
}

@keyframes type2 {
  0% {
    width: 0;
  }
  1% {
    opacity: 1;
  }
  99.9% {
    border-right: .15em solid orange;
  }
  100% {
    opacity: 1;
    border: none;
  }
}

@-webkit-keyframes type2 {
  0% {
    width: 0;
  }
  1% {
    opacity: 1;
  }
  99.9% {
    border-right: .15em solid orange;
  }
  100% {
    opacity: 1;
    border: none;
  }
}

@keyframes type3 {
  0% {
    width: 0;
  }
  1% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes type3 {
  0% {
    width: 0;
  }
  1% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}

@keyframes blink {
  50% {
    border-color: transparent;
  }
}
@-webkit-keyframes blink {
  50% {
    border-color: tranparent;
  }
}

